



















































.star {
    &-score {
        display: flex;
        justify-items: center;
        align-items: center;

        &__star {
            position: relative;

            img {
                width: 24px;
            }

            .star-item {
                display: flex;

                > img {
                    margin-left: 6px;
                }

                &.fill {
                    position: absolute;
                    left: 0;
                    top: 0;
                }
            }
        }
    }
}
